import { Box } from "@material-ui/core";
import { ApplicantsFilterTab } from "./ApplicantsFilterTab";
import { Applicants } from "./Applicants";
import { useLocation } from "react-router";
import axios from "axios";
import { useEffect, useState } from "react";
import { Application } from "../../models/index";
import { useDispatch } from "react-redux";
import { useApplicants } from "../../state/selectors";
import {
  setApplicants,
  setSelectedCampaignId,
  setSelectedCampaign,
} from "../../state/action-creators";

export function ViewApplicants() {
  const location = useLocation();
  const dispatch = useDispatch();
  const aplicantsList = useApplicants();

  const campaignId = location.pathname.split("/")[4];

  const [aplicants, setaplicants] = useState<Application[]>([]);

  const getApplicants = () => {
    const token = localStorage.getItem("token");
    axios
      .get("https://devapi.wobb.ai/api/user/info" + campaignId, {
        headers: { Authorization: token },
      })
      .then((res) => {
        console.log("this is data : ", res.data);
        setaplicants(res.data);
        dispatch(setSelectedCampaignId(campaignId));
        dispatch(setApplicants(res.data));
      });
  };

  const getCampaigndetails = () => {
    const token = localStorage.getItem("token");
    axios
      .get("/api/campaign/id/" + campaignId, {
        headers: { Authorization: token },
      })
      .then((res) => {
        dispatch(setSelectedCampaign(res.data[0]));
      });
  };
  useEffect(() => {
    getApplicants();
    getCampaigndetails();
  }, []);

  return (
    <Box paddingX={4}>
      <Box marginY={6}>
        <ApplicantsFilterTab aplicantsList={aplicantsList} />
      </Box>
      <Box marginY={2}>
        <Applicants />
      </Box>
    </Box>
  );
}
