import {
  Box,
  Checkbox,
  Grid,
  Paper,
  TableContainer,
  withStyles,
} from "@material-ui/core";
import { ApplicantsFilter } from "./ApplicantsFilter";
import * as XLSX from "xlsx";
import { AppMetadata } from "../ui/AppMetadata";
import { ApplicantsTable } from "./ApplicantsTable";
import { useUrlQuery } from "../../core/hooks";
import { __tr } from "../../i18n";
import { TextTransformNoneButton } from "../ui/TextTransformNoneButton";
import { CssVariables } from "../../css-variables";
import { OrderInfluencerSummary } from "./OrderInfluencerSummary";
import { NextMilestonesPay } from "./NextMilestonesPay";
import { DeliverableDashboard } from "./DeliverableDashboards";
import { useCallback, useEffect } from "react";
import { SendMessageToApplicants } from "../messaging/MessageToApplicantsDialog";
import { useState } from "react";
import { ShareBriefDialog } from "../messaging/ShareBriefDialog";
import { MarkCampaignAsCompleteDialog } from "./MarkCampaignAsCompleteDialog";
import { CampaignStatusNavigation } from "./CampaignStatusNavigation";
import { ApproveMilestones } from "./ApproveMilestones";
import { Application } from "../../models/index";
import { useApplicants, useApplicantsApprove } from "../../state/selectors";
import { DeliverableList } from "./Deliverables";
import { propTypes } from "react-bootstrap/esm/Image";
import { Refresh } from "@material-ui/icons";

const WhiteButton = withStyles({
  root: {
    background: "white",

    "&:hover": {
      background: CssVariables.colorPrimary,
      color: "white",
    },
  },
})(TextTransformNoneButton);

interface InfluencerFilter {
  sortBy?: string;
  followerRange: {
    min: number;
    max?: number;
  };
  engagementRate: number;
  category?: string[];
  gender?: string;
  age: number;
  location?: string[];
  language?: string[];
}

export function Applicants() {
  const [sendingMessage, setSendingMessage] = useState(false);
  const [forApprova, setForApproval] = useState(false);
  const [filterapplied, setfilterapplied] = useState(false);
  const [filterlist, setfilterlist] = useState<Application[]>([]);
  const [sharingBrief, setSharingBrief] = useState(false);
  const [markAsComplete, setMarkAsComplete] = useState(false);
  const [selected, setselected] = useState<string[]>([]);
  const [checked, setchecked] = useState(false);
  const [activatedBox, setactivatedBox] = useState(false);
  const [approveBox, setapproveBox] = useState(false);
  const filter = useUrlQuery("filter", "received");
  const status = useUrlQuery("status", "all");

  //const [approveList, setapproveList] = useState([]);
  const aplicantsList = useApplicants();

  const approveList = useApplicantsApprove();
  useEffect(() => {
    setactivatedBox(false);
    setapproveBox(false);
  }, []);

  const toActivate = approveList.filter((a) => a.actionOn === "Inactive") ?? [];

  const toAapprove =
    approveList.filter(
      (a) =>
        a.actionOn === "Activated" ||
        a.actionOn === "Submitted" ||
        a.actionOn === "Revision"
    ) ?? [];

  const approve = (ans: boolean) => {
    setForApproval(ans);
  };
  const selectAllCount = aplicantsList.filter(
    (a) => a.applicationStatus === filter
  ).length;

  const filteredApplicants = aplicantsList.filter(
    (a) => a.applicationStatus === filter
  );

  const applyFilter = (val: InfluencerFilter) => {
    setfilterapplied(true);
    const ans = filteredApplicants.filter((a) => {
      let flag = true;
      if (val.followerRange.min) {
        flag = Number(a.followers) > val.followerRange.min;
      }
      if (flag && val.followerRange.max) {
        flag = Number(a.followers) < val.followerRange.max;
      }
      if (flag && val.age) {
        flag = Number(a.age) >= val.age;
      }
      if (flag && val.language && val.language.length > 0) {
        val.language.forEach((l) => {
          flag = a.language?.includes(l) ?? false;
        });
      }
      if (flag && val.location && val.location?.length > 0) {
        val.location.forEach((l) => {
          flag = a.location?.includes(l) ?? false;
        });
      }
      if (flag && val.category && val.category?.length > 0) {
        val.category.forEach((c) => {
          flag = a?.categories.includes(c as Category) ?? false;
        });
      }
      if (flag && val.gender) {
        console.log("a.gender", a.gender);
        console.log("val.gender", val.gender);
        flag = a.gender === val.gender;
      }
      if (flag && val.engagementRate) {
        flag = Number(a.engagement) > Number(val.engagementRate);
      }
      console.log(flag);
      return flag;
    });
    console.log("aplicantsList", filteredApplicants);
    console.log("ans", ans);
    setfilterlist(ans);
  };

  const checkchange = (a: boolean, b: string) => {
    if (a) {
      setselected([...selected, b]);
    } else {
      const newl = selected.filter((i) => i !== b);
      setselected(newl);
    }
  };
  const selectAll = () => {
    if (checked) {
      setselected([]);
      setchecked(false);
    } else {
      filteredApplicants.forEach((s) => {
        const id: string = s._id ?? "";
        selected.push(id);
        console.log("selected", selected);
        setselected(selected);
      });
      setchecked(true);
    }
  };
  const onSendMessage = useCallback(() => {
    setSendingMessage(true);
  }, []);

  const onShareBrief = useCallback(() => {
    setSharingBrief(true);
  }, []);

  const onMarkAsComplete = useCallback(() => {
    setMarkAsComplete(true);
  }, []);

  const formatDate = (dt) => {
    const d = new Date(dt);
    let month = `${d.getMonth() + 1}`;
    if (month.length === 1) {
      month = `0${month}`;
    }
    let day = `${d.getDate()}`;
    if (day.length === 1) {
      day = `0${day}`;
    }
    return [d.getFullYear(), month, day].join("-");
  };
  const exportToExcel = () => {
    if (filteredApplicants?.length > 0) {
      const wb = XLSX.utils.book_new();
      let fileName = "Applicants.xlsx";
      const data = [];
      filteredApplicants.forEach((d) => {
        data.push({
          "Appilcation Id": d._id,
          "Influencer Name": d.influencerName,
          "Application Status": d.applicationStatus,
          "Social Account": d.socialaccount,
          Followers: d.followers,
          Engagement: d.engagement,
          Gender: d.gender,
          Age: d.age,
          Location: d.location,
          "Created At": formatDate(d.createdAt),
        });
        // data.push(d);
      });
      const ws = XLSX.utils.json_to_sheet(data);
      const wscols = [
        { wch: 25 },
        { wch: 20 },
        { wch: 20 },
        { wch: 15 },
        { wch: 10 },
        { wch: 15 },
        { wch: 10 },
      ];

      ws["!cols"] = wscols;
      ws["A1"].s = {
        font: {
          sz: 24,
          bold: true,
          color: { rgb: "FFAA00" },
        },
      };
      console.log(ws);
      XLSX.utils.book_append_sheet(wb, ws, "as");
      XLSX.writeFile(wb, fileName);
    } else {
      alert("Oops! nothing to export");
    }
  };

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}>
        <Box
          marginLeft={2}
          display="flex"
          flexDirection="row"
          alignItems="center"
          justifyContent="flex-start"
        >
          {filter !== "hired" && (
            <Box paddingRight={2}>
              <div>
                <Checkbox onClick={() => selectAll()} checked={checked} />
                <span>
                  {__tr("selectAll")} ({selectAllCount})
                </span>
              </div>
            </Box>
          )}
          <Box paddingX={0.5}>
            <WhiteButton
              disableElevation
              variant="contained"
              onClick={() => {
                exportToExcel();
              }}
            >
              {__tr("export")}
            </WhiteButton>
          </Box>

          {filter === "hired" && (
            <Box paddingRight={2}>
              <WhiteButton
                style={{ height: "38px" }}
                disableElevation
                variant="contained"
                onClick={() => {
                  if (toActivate.length > 0) {
                    setactivatedBox(true);
                    setapproveBox(false);
                  }
                }}
              >
                <span data-role="title">Activate Milestone</span>
                <div
                  data-role="badge"
                  style={{
                    background: "#D93F21",
                    margin: "8px",
                    color: "white",
                    width: "23px",
                    height: "23px",
                    borderRadius: "24px",
                  }}
                >
                  <span>{toActivate.length}</span>
                </div>
              </WhiteButton>
            </Box>
          )}

          {filter === "hired" && (
            <Box paddingRight={2}>
              <WhiteButton
                style={{ height: "38px" }}
                disableElevation
                variant="contained"
                onClick={() => {
                  if (toAapprove.length > 0) {
                    setactivatedBox(false);
                    setapproveBox(true);
                  }
                }}
              >
                <span data-role="title">Approve Milestone</span>
                <div
                  data-role="badge"
                  style={{
                    background: "#D93F21",
                    margin: "8px",
                    color: "white",
                    width: "23px",
                    height: "23px",
                    borderRadius: "24px",
                  }}
                >
                  <span>{toAapprove.length}</span>
                </div>
              </WhiteButton>
            </Box>
          )}

          {filter === "shortlisted" && (
            <Box paddingX={0.5}>
              <WhiteButton disableElevation variant="contained">
                {__tr("reject")}
              </WhiteButton>
            </Box>
          )}
        </Box>
      </Grid>
      <Grid item xs={8}>
        <Paper elevation={0}>
          {filter === "hired" && status !== "all" && <DeliverableList />}
          {(filter !== "hired" || status === "all") && (
            <TableContainer>
              <ApplicantsTable
                filter={filter}
                aplicantsList={
                  filterapplied && filter === "received"
                    ? filterlist
                    : filteredApplicants
                }
                selected={selected}
                setForApproval={approve}
                checkchange={(a, b) => {
                  checkchange(a, b);
                }}
              />
            </TableContainer>
          )}
        </Paper>
      </Grid>
      <Grid item xs={4}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            {filter === "received" && (
              <Paper elevation={0}>
                <ApplicantsFilter
                  onApply={(val) => {
                    applyFilter(val);
                  }}
                  clearAll={() => {
                    applyFilter({
                      followerRange: {},
                      age: 21,
                      engagementRate: 0,
                      language: [],
                      location: [],
                    });
                  }}
                />
              </Paper>
            )}
            {/* <Paper elevation={0}>
              <DeliverableDashboard />
            </Paper> */}
            {filter === "shortlisted" && filteredApplicants?.length > 0 && (
              <Paper elevation={0}>
                <OrderInfluencerSummary aplicantsList={filteredApplicants} />
              </Paper>
            )}
            {filter === "hired" && activatedBox && toActivate.length > 0 && (
              <Paper elevation={0}>
                <NextMilestonesPay aplicantsList={toActivate} />
              </Paper>
            )}

            {filter === "hired" && approveBox && approveList.length > 0 && (
              <Paper elevation={0}>
                <ApproveMilestones aplicantsList={approveList} />
              </Paper>
            )}
            {/* <Paper elevation={0}>
              <CampaignStatusNavigation />
            </Paper> */}
          </Grid>
          <Grid item xs={12}>
            <AppMetadata />
          </Grid>
        </Grid>
      </Grid>
      <SendMessageToApplicants
        open={sendingMessage}
        onClose={() => setSendingMessage(false)}
        maxWidth="md"
        fullWidth
      />
      <ShareBriefDialog
        open={sharingBrief}
        onClose={() => setSharingBrief(false)}
        maxWidth="md"
        fullWidth
      />
      <MarkCampaignAsCompleteDialog
        open={markAsComplete}
        onClose={() => setMarkAsComplete(false)}
        maxWidth="md"
        fullWidth
      />
    </Grid>
  );
}
